header {
  background-color: #1d1d1d;
  width: 100%;
  height: auto;
  .sm(padding-top, 20px);
 
  .owl-carousel {
    position: relative;
    .carosel_img {
      width: 100%;
      height: 600px;
      position: relative;
      img {
        max-width: 100%;
        height: 600px;
      }
    
    }
   
    .owl-prev{
      position: absolute;
      top: 40%;
      left: 0px;
      width: 50px;
      height: 100px;
      background-color: #da491c !important;
      span{
        color: white;
        font-size: 40px;
        font-weight: 400;
      }
    }
    .owl-next{
      position: absolute;
      top: 40%;
      right: 0px;
      background-color: #da491c !important;
      width: 50px;
      height: 100px;
      span{
        color: white;
        font-size: 40px;
        font-weight: 400;
      }
    }
  }
   
}
