@import "./header.less";
@import "./section.less";
@import "./params/var.less";
@import "./params/mix.less";
@import "./footer.less";

img {
  max-width: 100%;
  height: auto;
}

body {
  font-family: Lato;
  .click_top {
    position: fixed;
    right: 90px;
    display: block;
    top: 200px;
    z-index: 111;
    .sm-block({
        right: 10px;
        top: 100px;
    });
    .scroll_top {
      color: #ff4848;
      background-color: black;
      font-size: 40px;
      border-radius: 5px;
      border: none;
      text-shadow: 2px 2px 2px black;
      .sm(font-size, 30px);
    }
  }
  .btn{
    padding: 15px 16px;
    background-color: #333333;
    border: none;
    text-transform: capitalize;
    font-weight: 700;
    font-size: 13px;
    text-align: center;
    color: white;
    transition: 500ms;
    line-height: 0px;

    &:hover{
      background-color: #ff4848;
      box-shadow: 2px 2px 2px black;
    }
  }
}
