footer {
  background-color: #333333;
  padding-top: 80px;
  padding-bottom: 60px;
  ul {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    li {
      margin: 0px 14px;
      a {
        text-decoration: none;
        text-transform: uppercase;
        color: white;
        font-size: 13px;
      }
    }
  }
  form {
    input {
      font-size: 60px;
      font-weight: 300;
      color: #4c4c4c;
      padding-bottom: 20px;
      border: none;
      border-bottom: 2px solid #929292;
      background-color: transparent;
      text-transform: capitalize;
    }
    p {
      color: white;
      text-transform: capitalize;
      font-size: 16px;
      margin-top: 15px;
    }
  }

  .last_text {
    font-size: 14px;
    margin-top: 60px;
    font-weight: 400;
    color: white;
    text-transform: capitalize;
  }
  h3{
      color: white;
      text-transform: uppercase;
      font-size: 700px;
      font-size: 26px;
      margin-bottom: 20px;
  }
  p{
      color: white;
      font-size: 14px;
      line-height: 30px;
      text-transform: capitalize;
  }
  .social_icon{
      margin-top: 35px;
      i{
          margin-right: 8px;
          color: black;
          border-radius: 50%;
          padding: 8px;
          background-color: white;
          font-size: 16px;
      }
  }
}
