section {
  padding-top: 170px;
  h2 {
    font-weight: 600;
    color: black;
    text-transform: uppercase;
    font-size: 36px;
    margin-bottom: 16px;
  }
  .links {
    display: flex;
    margin-bottom: 50px;
    p {
      font-size: 14px;
      color: #666666;
      text-transform: capitalize;
      margin-right: 15px;
      i {
        color: black;
        margin-right: 10px;
      }
      span {
        color: #da491c;
      }
    }
  }
  p {
    font-size: 14px;
    color: #666666;
    line-height: 30px;
    font-weight: 400;
  }
  .point_dali {
    margin: 40px 0px;
    border-top: 1px solid #d2d1cf;
    border-bottom: 1px solid #d2d1cf;
    padding: 25px 0px;
    p {
      color: #da491c;
      font-size: 30px;
      text-transform: capitalize;
      line-height: 40px;
    }
  }
  .social_icons {
    margin-top: 70px;
    a {
      text-decoration: none;
      font-size: 14px;
      color: white;
      padding: 5px;
      border-radius: 50%;
      i {
        padding: 10px;
        border-radius: 50%;
      }
      .fa-twitter {
        background-color: #0084b4;
      }
      .fa-facebook-f {
        background-color: #4267b2;
      }
      .fa-google-plus-g {
        background-color: #db4a39;
      }
      .fa-instagram {
        background-color: #f09433;
      }
      .fa-invision {
        background-color: #4267b2;
      }
    }
  }

  .comments {
    margin-top: 50px;
    h3 {
      font-weight: 600;
      font-size: 14px;
      color: black;
      text-transform: uppercase;
      margin-bottom: 50px;
      display: flex;
      justify-content: space-between;

      &::after {
        content: "";
        width: 85%;
        height: 1px;
        background-color: #666666;
        display: block;
        align-self: center;
      }
    }
    .box_imges {
      text-align: center;
      img {
        border-radius: 50%;
        text-align: center;
      }
    }
    .text_top {
      margin-bottom: 15px;
      display: flex;
      justify-content: space-between;
      p {
        color: #333333;
        font-weight: 700;
        font-size: 14px;
        text-transform: capitalize;
        span {
          font-weight: 400;
        }
      }
      .reply {
        font-weight: 400;
        i {
          margin-right: 5px;
        }
      }
    }
    .text_stbl {
      p {
        font-size: 14px;
        color: #666666;
        font-weight: 400;
        line-height: 30px;
      }
    }
  }
  .ml_50 {
    margin-left: 50px;
  }
  .mb_50 {
    margin-bottom: 50px;
  }
  .right_box {
    form {
      label {
        font-size: 14px;
        font-weight: 400;
        text-transform: capitalize;
        color: #333333;
        input {
          display: block;
          font-size: 14px;
          font-weight: 400;
          color: #333333;
          padding: 15px 15px 12px;
          margin-bottom: 50px;
          margin-top: 10px;
          width: 100%;
          line-height: 0px;
        }
      }
    }
    .right_btn {
      margin-bottom: 20px;
      display: flex;
      width: 100%;
      button {
        margin-top: 0;
      }
      .comments {
        margin-left: 5px;
        padding: 15px 7px;
      }
    }
    p {
      font-size: 14px;
      font-weight: 400;
      color: #333333;
      text-transform: capitalize;
      border-bottom: 1px solid #666666;
      padding-bottom: 12px;
    }
    .marketing {
      padding: 10px 17px;
      width: 100%;
      background-color: #333333;
      color: white;
      margin-bottom: 20px;
      border: none;
      line-height: 30px;
    }
    .right_text_box{
      margin-top: 20px;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      p{
        line-height: 20px;
        border: none;
      }
      img{
        height: 100%;
      }
      .box_texts{
        margin-left: 14px;
        max-width: 63%;
      }
      .comments_new{
        margin-top: 30px;
      }
      .coment{
        color: white;
        background-color: #333333;
        border: none;
        border-radius: 5px;
        padding: 6px 10px;
      }
    }
  }
}
